@font-face {
  font-family: 'Uni Neue';
  src: url('../fonts/UniNeueRegular.eot');
  src: local('Uni Neue Regular'), local('UniNeueRegular'),
    url('../fonts/UniNeueRegular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/UniNeueRegular.woff2') format('woff2'),
    url('../fonts/UniNeueRegular.woff') format('woff'),
    url('../fonts/UniNeueRegular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Uni Neue';
  src: url('../fonts/UniNeueBold.eot');
  src: local('Uni Neue Bold'), local('UniNeueBold'),
    url('../fonts/UniNeueBold.eot?#iefix') format('embedded-opentype'),
    url('../fonts/UniNeueBold.woff2') format('woff2'),
    url('../fonts/UniNeueBold.woff') format('woff'),
    url('../fonts/UniNeueBold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'ALS Rubl';
  src: url('../fonts/ALSRubl.woff2') format('woff2'),
    url('../fonts/ALSRubl.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}


html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent
}

a:hover,
a {
  text-decoration: none
}

:focus {
  outline: 0
}

ul {
  list-style-type: none;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%
}

body {
  font-family: 'Uni Neue';
  width: 100%;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  color: #000;
  background-color: #f7f7f7;
}




main {
  display: flex;
  min-width: 1280px;

  aside {
    position: relative;
    width: 240px;
    height: 100vh;
    z-index: 99;

  }

  .side-column {
    width: 100%;
    height: 100vh;
    background-color: #202020;
  }

  .content {
    width: calc(100% - 240px);
  }
}

.column-fixed {
  position: fixed;
  width: 240px;
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  background-color: #202020;
}

.user-name {
  display: flex;
  align-items: center;
  padding: 20px;

  &__avatar {
    margin-right: 20px;
    min-width: 40px;
    position: relative;

    span {}

    img {
      width: 40px;
      height: 40px;
      overflow: hidden;
      object-fit: cover;
      border-radius: 12px;
    }
  }

  &__name {
    font-size: 16px;
    font-weight: bold;
    line-height: 1.25;
    color: #ffffff;

  }
}

.user-stats {
  padding: 20px;
  background-color: #262626;
}

.stats-item {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

  &__title {
    display: block;
    font-size: 12px;
    line-height: 12px;
    color: #909090;
    margin-bottom: 2px;
  }

  &_text {
    display: block;
    font-size: 16px;
    font-weight: bold;
    line-height: 16px;
    color: #ffffff;
  }
}


.side-menu {
  padding: 20px;

  ul {
    li {
      position: relative;
      margin-bottom: 15px;
      padding-left: 30px;

      a {
        font-size: 16px;
        line-height: 20px;
        color: #909090;
        transition: 0.5s;
      }

      &:hover {
        a {
          color: #fff;
        }

        .icon {
          filter: grayscale(0%);
        }
      }

      &.current {
        a {
          color: #fff;
        }

        .icon {
          filter: grayscale(0%);
        }
      }
    }
  }


  .icon {
    position: absolute;
    width: 20px;
    text-align: center;
    height: 20px;
    left: 0;
    top: 0;
    color: #909090;
    filter: grayscale(100%);
    transition: 0.5s;

    img {
      max-width: 100%;
    }
  }
}

.logo {
  img {
    width: 125px;
  }
}

.side-logo {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

