}


.table {
  margin-bottom: 30px;

  table {
    width: 100%;
    border-collapse: collapse;
  }

  tr {
    &:last-child {
      td {
        border-bottom: 0;
      }

      td {
        &:first-child {
          border-radius: 0 0 0 10px;
        }
      }

      td {
        &:last-child {
          border-radius: 0 0 10px 0;
        }
      }
    }

    &:hover td {
      background: #d8d8d8;
    }
  }

  th {
    border-bottom: 1px solid #fff;
    vertical-align: middle;

    span {
      position: relative;

      &:before {
        content: "";
        width: 14px;
        height: 14px;
        background: url(../images/arrow-down.svg);
        position: absolute;
        right: -24px;
        top: calc(50% - 7px);
        filter: grayscale(100%);
        cursor: pointer;
      }

      &.current {
        &:before {
          filter: grayscale(0)
        }
      }
    }

    font-size: 14px;
    line-height: 16px;
    color: #ffffff;
    background-color: #202020;
    padding: 15px 20px;
    text-align: left;

    &:first-child {
      border-radius: 10px 0 0 0;
    }

    &:last-child {
      border-radius: 0 10px 0 0;
    }
  }



  td {
    border-bottom: 1px solid #fff;
    background-color: #eeeeee;
    padding: 15px 20px;
    text-align: left;
    font-size: 14px;
    line-height: 16px;
    color: #505050;
    vertical-align: middle;
  }


}

