}

.form-control {
  &--min {
    width: 265px !important;

  }

  &--checked {
    background-image: url(../images/tarif-cheked.svg);
    background-position: calc(100% - 15px) 50%;
    background-repeat: no-repeat;
  }
}

.content-name {
  position: absolute;
  top: 30px;
  left: 0px;
  padding-bottom: 5px;
  transition: all 0.3s ease;

}

.form-group input {
  &:focus {
    outline: none;

    + .label-name .content-name {
      font-size: 12px;
      left: 0px;
      top: 16px;
      bottom: auto;
    }

    + .label-name.center .content-name {
      left: -15px;
      width: 100%;
      text-align: center;
      left: -15px;
      display: block;
      width: 100%;
    }
  }

  &:valid + .label-name .content-name {
    font-size: 12px;
    left: 0px;
    top: 16px;
  }

  &:valid + .label-name.center .content-name {
    text-align: center;
    left: -15px;
  }

  &:focus + .label-name::after,
  &:valid + .label-name::after {
    font-size: 12px;
    left: 0px;
    top: 16px;
  }
}

.label-name.center .content-name {
  left: -15px;
  width: 100%;
  text-align: center;
  left: -15px;
  display: block;
  width: 100%;
}





.form-group textarea {
  &:focus {
    outline: none;

    + .label-name .content-name {
      font-size: 12px;
      left: 0px;
      top: 12px;
    }
  }

  &:valid + .label-name .content-name {
    font-size: 12px;
    left: 0px;
    top: 12px;
  }

  &:focus + .label-name::after,
  &:valid + .label-name::after {
    font-size: 12px;
    left: 0px;
    top: 12px;
  }
}


