/*Styling Selectbox*/

.drop {
  width: 100% !important;
  color: #000;
  position: relative;
  transition: 0.5s;
  border-radius: 10px;
  background: #fff;
  overflow: hidden;
  margin-bottom: 20px;

}

.option span {
  display: block;
  font-size: 12px;
  line-height: 12px;
  color: #b4b4b4;
  padding-top: 4px;
}

.drop {
  max-height: 300px;
  overflow-y: auto;

  .option {
    padding: 0 10px;
    height: 50px;
    cursor: pointer;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    background-color: #ffffff;

    &:not(.active) {
      display: none;
      opacity: 0;
    }

    &.active {}

    .option-item {
      padding: 5px;
    }
  }

  .option2 {
    padding: 0 10px;
    height: 50px;
    cursor: pointer;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    background-color: #ffffff;

    &:not(.active) {
      display: none;
      opacity: 0;
    }

    &.active {}

    .option-item {
      padding: 5px;
    }
  }

  &.visible {


    &:before,
    &:after {
      border-color: #fff;
    }

    &:before {
      opacity: 0;
    }

    &:after {
      opacity: 1;
    }

    .option {
      display: block;
    }
  }

  &.opacity .option {
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    opacity: 1;
  }



  &.withBG .option {
    transition: background-color 0.1s;

    &:not(.placeholder) {
      &:hover .option-item {
        background-color: #f7f7f7;
      }
    }

    &:not(.placeholder) {
      &.active {
        background-image: url(../images/check-select.svg);
        background-position: calc(100% - 15px) 50%;
        background-repeat: no-repeat;
      }
    }

  }

  &:after {
    content: "";
    position: absolute;
    top: 17px;
    right: 15px;
    width: 20px;
    height: 20px;
    background: url(../images/arrow-up.svg);
    transition: 0.2s;
  }

  &:before {
    content: "";
    position: absolute;
    top: 17px;
    right: 15px;
    width: 20px;
    height: 20px;
    background: url(../images/arrow-up.svg);
    transition: 0.2s;
    transform: rotate(-180deg);
  }

  &:after {
    border-right: none;
    border-bottom: none;
    opacity: 0;
  }
}



